.container {
  width: 100%;
}

@media (min-width: 640px) {

  .container {
    max-width: 640px;
  }
}

@media (min-width: 768px) {

  .container {
    max-width: 768px;
  }
}

@media (min-width: 1024px) {

  .container {
    max-width: 1024px;
  }
}

@media (min-width: 1280px) {

  .container {
    max-width: 1280px;
  }
}

@media (min-width: 1536px) {

  .container {
    max-width: 1536px;
  }
}

.mx-8 {
  margin-left: 2rem;
  margin-right: 2rem;
}

.h-full {
  height: 100%;
}

.w-full {
  width: 100%;
}

.bg-secondary {
  --tw-bg-opacity: 1;
  background-color: rgb(47 98 113 / var(--tw-bg-opacity));
}

.text-2xl {
  font-size: 1.5rem;
  line-height: 2rem;
}

.uppercase {
  text-transform: uppercase;
}

.text-ornament {
  --tw-text-opacity: 1;
  color: rgb(186 140 78 / var(--tw-text-opacity));
}

.header {
  .top-bar {
    height: 2rem;
  }
  .top-bar {
    width: 100%;
  }
  .top-bar {
    --tw-bg-opacity: 1;
    background-color: rgb(186 140 78 / var(--tw-bg-opacity));
  }

  .bottom-bar {
    height: 6rem;
  }

  .bottom-bar {
    width: 100%;
  }

  .bottom-bar {
    border-bottom-width: 2px;
  }

  .bottom-bar {
    --tw-border-opacity: 1;
    border-color: rgb(186 140 78 / var(--tw-border-opacity));
  }

  .bottom-bar {
    --tw-bg-opacity: 1;
    background-color: rgb(50 54 73 / var(--tw-bg-opacity));
  }

  .content {
    margin-left: auto;
    margin-right: auto;
  }

  .content {
    height: 6rem;
  }

  .content {
    width: 1120px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .logo {
    height: 8rem;
  }

  .logo {
    width: auto;
  }
}

.page-content {
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
  height: 100%;
  border-width: 2px;
  --tw-border-opacity: 1;
  border-color: rgb(186 140 78 / var(--tw-border-opacity));
  padding: 0.5rem;
  width: 80rem; 
  height: 52rem;
  display: flex;
  justify-content: space-between;

  .left-ornaments, .right-ornaments {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    img {
      height: 9rem;
    }

    img {
      width: 9rem;
    }
  }

  .left-ornaments {
    .ornament-bottom {
      transform: scaleY(-1);
    }
  }

  .right-ornaments {
    transform: scaleX(-1);

    .ornament-bottom {
      transform: scaleY(-1);
    }
  }
}

.home-menu {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .menu-item {
    margin-bottom: 1rem;
  }

  .menu-item {
    border-width: 2px;
  }

  .menu-item {
    --tw-border-opacity: 1;
    border-color: rgb(186 140 78 / var(--tw-border-opacity));
  }

  .menu-item {
    --tw-bg-opacity: 1;
    background-color: rgb(50 54 73 / var(--tw-bg-opacity));
  }

  .menu-item {
    padding: 2rem;
  }

  .menu-item {
    width: 48rem;
    height: 14rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;

    .menu-item-icon {
      margin-top: auto;
      margin-bottom: auto;
    }

    .menu-item-icon {
      height: 100%;
    }

    .menu-item-icon {
      width: 12rem;
    }

    .menu-item-icon {

      img {
        height: 100%;
      }

      img {
        width: 100%;
      }
    }

    .menu-item-content {
      height: 100%;
    }

    .menu-item-content {
      --tw-text-opacity: 1;
      color: rgb(186 140 78 / var(--tw-text-opacity));
    }

    .menu-item-content {

      h3 {
        margin-top: 1rem;
        margin-bottom: 1rem;
      }

      h3 {
        font-size: 1.5rem;
        line-height: 2rem;
      }

      p {
        font-size: 1.25rem;
        line-height: 1.75rem;
      }
    }
  }
}
